import React, { PureComponent } from 'react';
import { Form, Input, Button } from 'antd';
import { withRouter } from 'react-router-dom';
import { connect } from "react-redux";
import { actionCreators } from '../../store';

class EditRule extends PureComponent {

  render() {
    const { book, price, start, end, handlePriceChange, handleStartChange, handleEndChange, handleCreate, handleUpdate, location } = this.props;
    const FormLabelCol = {
      labelCol: {
        sm: {span: 3},
      },
      wrapperCol: {
        sm: {span: 8},
      }
    };
    let submit = location.pathname === '/build/novel/config/create' ? <Button style={{marginRight: '20px'}} type='primary' onClick={() => {handleCreate(this.props)}}>创建</Button> : <Button style={{marginRight: '20px'}} type='primary' onClick={() => {handleUpdate(this.props)}}>修改</Button>;
    return (
      <Form {...FormLabelCol}>
        <Form.Item label='小说名称'>
          <h3>{book.title}</h3>
        </Form.Item>
        <Form.Item label='付费金额'>
          <Input value={price} placeholder='请输入付费金额' onChange={handlePriceChange} />
        </Form.Item>
        <Form.Item label='起始章节'>
          <Input value={start} placeholder='请输入起始章节' onChange={handleStartChange} />
        </Form.Item>
        <Form.Item label='结束章节'>
          <Input value={end} placeholder='请输入结束章节' onChange={handleEndChange} />
        </Form.Item>
        <Form.Item style={{textAlign: 'center'}}>
          {submit}
          <Button type='danger' onClick={() => {this.props.history.goBack();}}>取消</Button>
        </Form.Item>
      </Form>
    )
  }
  componentDidMount() {
    console.log(this.props)
    const { book, location, initUpdateForm } = this.props;
    if (this.props.location.pathname === '/build/novel/config/update') {
      initUpdateForm(location.query)
    }
    this.props.setBookInfo(book.id, book.title)
  }
  componentWillUnmount() {
    this.props.resetState()
  }
}

const mapState = (state) => ({
  book: state.novel.book,
  price: state.novel.price,
  start: state.novel.start,
  end: state.novel.end,
  rule_id: state.novel.rule_id
});

const mapDispatch = (dispatch) => ({
  setBookInfo(id, title) {
    dispatch(actionCreators.setBookInfo(id, title))
  },
  initUpdateForm(data) {
    dispatch(actionCreators.initUpdateForm(data))
  },
  handlePriceChange(e) {
    let price = e.target.value;
    dispatch(actionCreators.handlePriceChange(price))
  },
  handleStartChange(e) {
    let start = e.target.value;
    dispatch(actionCreators.handleStartChange(start))
  },
  handleEndChange(e) {
    let end = e.target.value;
    dispatch(actionCreators.handleEndChange(end))
  },
  handleCreate(props) {
    dispatch(actionCreators.createRules(props))
  },
  handleUpdate(props) {
    dispatch(actionCreators.updateRules(props))
  },
  resetState() {
    dispatch(actionCreators.resetState())
  }
});

export default connect(mapState, mapDispatch)(withRouter(EditRule));